.Sidebar{
    display: -ms-flexbox;
    display: flex;
    height:100vw;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: rgb(111, 175, 85);
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}


.straight_line{
    -ms-flex-negative: 1;
        flex-shrink: 1;
}

@media(max-width : 630px){
    .Sidebar{
        height:100vh;
    }
}
@media(max-width : 565px){
    .Sidebar{
        display: none;
    }
}






